.logpresso-icon-item i {
	font-size: 36px;
}
/* box color : #42444a */
:root {
	--bg: #24292e;
	--box-bg: #42444a;
	--placeholder-color: #72747a;
	--box-radius: 5px;
}

body {
	background:  var(--bg);
	color: #fff;
	font-family: 'Open Sans', sans-serif;
}

* {
	color: #fff;
}

.header-area h1 {
	display: flex;
}

.header-area img {
	width: 30px;
	margin-right: 10px;
}

.hint-area {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 20px
}

.hint-area xmp {
	font-size: 1rem;
	color: var(--placeholder-color);
}

.content-area {
	display: flex;
	padding-left: 10px;
}

.content-area .main-area {
	flex: 1;
}

.content-area .search-bar {
	display: flex;
	flex: 1;
	background: var(--box-bg);
	border-radius: var(--box-radius);
	max-height: 30px;
	padding: 10px;
	align-items: center;
	font-size: 1.4rem;
}

.content-area .search-bar input {
	background: 0;
	border: none;
	margin-left: 15px;
	font-size: 1.2rem;
	flex: 1;
}

.content-area .search-bar input:focus {
	outline: none;
}

.custom-icon {
	padding: 0 30px;
	flex: 0.4;
	flex-direction: column;
}

.custom-icon h2 {
	margin: 0;
}

.custom-icon .custom-wrapper {
	margin-top: 20px;
}

.custom-icon input {
	padding: 5px 10px;
	flex: 1 0 auto;
	margin-right: 10px;
	background-color: var(--box-bg);
	border: 0;
	border-radius: var(--box-radius);
}

.custom-icon .custom-input-container {
	display: flex;
	align-items: center;
}

.custom-icon .custom-input-container .icon-color-preview { 
	width: 25px;
	height: 25px;
	border-radius: var(--box-radius);
	background-color: #fff;
}

label {
	display: block;
	cursor: pointer;
	color: var(--placeholder-color);
}

.logpresso-icon-viewer {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-top: 20px;
	gap: 30px;
}

.logpresso-icon-item {
	display: flex;
	flex-direction: column;
	text-align: center;
	width: 130px;
	height: 130px;
	padding: 10px;
	background-color: var(--box-bg);
	border-radius: var(--box-radius);
	cursor: pointer;
}

.logpresso-icon-item i {
	display: flex;
	flex: 1;
}